import { Heading } from '@/checkout/components';
import { useBreakpoint } from '@/checkout/hooks/useBreakpoint';
import { Text } from '@adobe/elsie/i18n';
import { VComponent, classes } from '@adobe/elsie/lib';
import { FunctionComponent, VNode } from 'preact';
import { HTMLAttributes } from 'preact/compat';

import '@/checkout/components/Checkout/Checkout.css';

const ConditionalWrapper: FunctionComponent<{
  children?: VNode;
  className: string;
}> = ({ children, className }) => {
  const breakpoint = useBreakpoint();
  if (breakpoint === 'small') return <>{children}</>;
  return <div className={className}>{children}</div>;
};

export type AsideSections = {
  cartSummary: VNode;
  orderSummary: VNode;
};

const Aside: FunctionComponent<{ sections: AsideSections }> = ({
  sections,
}) => {
  return (
    <ConditionalWrapper className="checkout__aside">
      <>
        <VComponent node={sections.orderSummary} />
        <VComponent node={sections.cartSummary} />
      </>
    </ConditionalWrapper>
  );
};

export type MainSections = {
  billingAddress: VNode;
  billToShippingAddress?: VNode;
  login: VNode;
  paymentMethods: VNode;
  placeOrder: VNode;
  shippingAddress?: VNode;
  shippingMethods?: VNode;
};

const MainContent: FunctionComponent<MainSections> = ({
  billingAddress,
  billToShippingAddress,
  login,
  paymentMethods,
  placeOrder,
  shippingAddress,
  shippingMethods,
}) => {
  return (
    <>
      <VComponent node={login} />
      {shippingAddress && <VComponent node={shippingAddress} />}
      {billToShippingAddress && <VComponent node={billToShippingAddress} />}
      {shippingMethods && <VComponent node={shippingMethods} />}
      <VComponent node={paymentMethods} />
      <VComponent node={billingAddress} />
      <VComponent node={placeOrder} />
    </>
  );
};

const Main: FunctionComponent<{
  outOfStock?: VNode;
  sections?: MainSections;
}> = ({ outOfStock, sections }) => {
  return (
    <ConditionalWrapper className="checkout__main">
      <>
        <Heading level={1} className="checkout-title">
          <Text id="Checkout.title" />
        </Heading>

        {outOfStock && (
          <VComponent className="checkout-outOfStock" node={outOfStock} />
        )}

        {sections && <MainContent {...sections} />}
      </>
    </ConditionalWrapper>
  );
};

export interface CheckoutProps extends HTMLAttributes<HTMLDivElement> {
  banner?: VNode;
}

interface CheckoutComponent extends FunctionComponent<CheckoutProps> {
  Main: typeof Main;
  Aside: typeof Aside;
}

export const Checkout: CheckoutComponent = (props) => {
  const { banner, className, children, ...restProps } = props;

  return (
    <div className={classes(['checkout', className])} {...restProps}>
      {banner && <VComponent className="checkout__banner" node={banner} />}
      <div className="checkout__content">{children}</div>
    </div>
  );
};

Checkout.Main = Main;
Checkout.Aside = Aside;
