import {
  ValidationRule,
  ValidationRuleEnum,
} from '@/checkout/__generated__/types';

export type ValidityProps = {
  accept?: string;
  max?: string | number;
  maxLength?: number;
  min?: string | number;
  minLength?: number;
  pattern?: string;
  required?: boolean;
};

export type InputValidation =
  | 'alphanumeric'
  | 'alphanumeric-w-space'
  | 'alphanum-with-spaces'
  | 'alpha'
  | 'url'
  | 'numeric'
  | 'email';

export const validityProps = (
  rules: ValidationRule[]
): ValidationRule & ValidityProps =>
  rules.reduce((props, rule) => {
    switch (rule.name) {
      case ValidationRuleEnum.DateRangeMax:
        return { ...props, max: rule.value };
      case ValidationRuleEnum.DateRangeMin:
        return { ...props, min: rule.value };
      case ValidationRuleEnum.FileExtensions:
        return { ...props, accept: rule.value };
      case ValidationRuleEnum.InputValidation:
        return {
          ...props,
          pattern: patternFor(rule.value as InputValidation),
        };
      case ValidationRuleEnum.MaxFileSize:
      case ValidationRuleEnum.MaxImageHeight:
      case ValidationRuleEnum.MaxImageWidth:
        return props;
      case ValidationRuleEnum.MaxTextLength:
        return { ...props, maxLength: rule.value };
      case ValidationRuleEnum.MinTextLength:
        return { ...props, minLength: rule.value };
      default:
        throw new Error(`Unknown rule: ${rule.name}`);
    }
  }, {});

export type Patterns = {
  [key in InputValidation]: RegExp;
};

// TODO allow to override patterns
export const patterns: Patterns = {
  alpha: /^[a-zA-Z]+$/,
  alphanumeric: /^[a-zA-Z0-9]+$/,
  'alphanumeric-w-space': /^[a-zA-Z0-9 ]+$/,
  'alphanum-with-spaces': /^[a-zA-Z0-9 ]+$/,
  email:
    /^([a-z0-9,!#$%&'*+/=?^_`{|}~-]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+(\.([a-z0-9,!#$%&'*+/=?^_`{|}~-]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+)*@([a-z0-9-]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+(\.([a-z0-9-]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+)*\.(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]){2,})$/i,
  numeric: /^[0-9]+$/,
  url: /^((([A-Za-z]{3,9}:(?:\/\/)?)(?:[-;:&=+$,\w]+@)?[A-Za-z0-9.-]+|(?:www\.|[-;:&=+$,\w]+@)[A-Za-z0-9.-]+)((?:\/[+~%/.\w\-_]*)?\??(?:[-+=&;%@.\w_]*)#?(?:[.!/\\\w]*))?)$/,
};

const patternFor = (type: InputValidation): string => {
  switch (type) {
    case 'alpha':
      return patterns.alpha.source;
    case 'alphanumeric':
      return patterns.alphanumeric.source;
    case 'alphanumeric-w-space':
      return patterns['alphanumeric-w-space'].source;
    case 'alphanum-with-spaces':
      return patterns['alphanum-with-spaces'].source;
    case 'url':
      return patterns.url.source;
    case 'numeric':
      return patterns.numeric.source;
    case 'email':
      return patterns.email.source;
    default:
      throw new Error(`Unknown validation type: ${type}`);
  }
};
