import { useCallback, useEffect, useState } from 'preact/hooks';
import { useServices } from '@/checkout/context';
import { events } from '@adobe/event-bus';
import { DEBOUNCE_TIME } from './constants';
import {
  AddressFormTypes,
  AddressFormValues,
} from '@/checkout/data/models/address-form';

export function useAddressBackup(addressType: AddressFormTypes) {
  const { backupService } = useServices();
  const [addressBackup, setAddressBackup] = useState<AddressFormValues | null>(
    null
  );

  // restore address from local storage
  useEffect(() => {
    const backup = backupService.restore<AddressFormValues>(addressType);
    if (!backup) return;
    setAddressBackup(backup);
  }, [addressType, backupService]);

  // remove backup when the order is placed
  useEffect(() => {
    const onOrder = events.on('checkout/order', () => {
      backupService.remove(addressType);
    });

    return () => {
      onOrder?.off();
    };
  }, [addressType, backupService]);

  const backup = useCallback(
    (address: AddressFormValues) => {
      return setTimeout(() => {
        // we do not want to update the state to avoid re-rendering
        backupService.backup<AddressFormValues>(addressType, address);
      }, DEBOUNCE_TIME);
    },
    [addressType, backupService]
  );

  const removeBackup = useCallback(() => {
    backupService.remove(addressType);
  }, [addressType, backupService]);

  return { addressBackup, backup, removeBackup };
}
