import { PreselectedMappedField } from '@/checkout/components/AddressForm';
import { DEBOUNCE_TIME } from '@/checkout/components/AddressForm/constants';
import { useAddressBackup } from '@/checkout/components/AddressForm/useAddressBackup';
import {
  AddressFormErrors,
  AddressFormTypes,
  AddressFormValues,
} from '@/checkout/data/models';
import {
  Translations as ValidityTranslations,
  useValidity,
} from '@/checkout/hooks/useValidity';
import { cartSignal } from '@/checkout/signals';
import { isEmpty } from '@/checkout/utils';
import { useText } from '@adobe/elsie/i18n';
import { getFormErrors } from '@adobe/elsie/lib';
import { RefObject } from 'preact';
import { useCallback, useEffect, useRef, useState } from 'preact/hooks';

export type DefaultValues = {
  country_id?: string;
};

export type AddressFormState = {
  address: AddressFormValues;
  errors: AddressFormErrors;
};

export type SaveAddressCommand = {
  signal?: AbortSignal;
  address: AddressFormValues;
};

export type UseAddressFormProps = {
  formRef: RefObject<HTMLFormElement>;
  type: AddressFormTypes;
  defaultValues?: DefaultValues;
  preselection?: PreselectedMappedField | null;
  saveAddressHandler: (
    command: SaveAddressCommand
  ) => Promise<typeof cartSignal.value.data>;
};

const isFormValid = (formRef: RefObject<HTMLFormElement>): boolean => {
  const form = formRef.current;
  if (!form) return false;
  const errors = getFormErrors(form);
  return isEmpty(errors);
};

export const useAddressForm = ({
  formRef,
  type,
  defaultValues = {},
  preselection = {},
  saveAddressHandler,
}: UseAddressFormProps) => {
  const translations = useText({
    badInput: 'Checkout.AddressForm.Validity.badInput',
    patternMismatch: 'Checkout.AddressForm.Validity.patternMismatch',
    rangeUnderflow: 'Checkout.AddressForm.Validity.rangeUnderflow',
    tooLong: 'Checkout.AddressForm.Validity.tooLong',
    tooShort: 'Checkout.AddressForm.Validity.tooShort',
    typeMismatch: 'Checkout.AddressForm.Validity.typeMismatch',
    valueMissing: 'Checkout.AddressForm.Validity.valueMissing',
  }) as ValidityTranslations;

  const hasUnsavedChanges = useRef(false);
  const [address, setAddress] = useState<AddressFormValues>({});

  const { addressBackup, backup, removeBackup } = useAddressBackup(type);

  const { errors, validateFormElement } = useValidity(translations);

  const saveAddressForm = useCallback(
    (command: SaveAddressCommand) => {
      hasUnsavedChanges.current = false;
      saveAddressHandler(command)
        .then(() => {
          removeBackup();
        })
        .catch((error) => {
          hasUnsavedChanges.current = true;
          console.error('Saving address form failed:', error);
        });
    },
    [removeBackup, saveAddressHandler]
  );

  const updateField = (code: string, value: string) => {
    setAddress((prevAddress) => ({
      ...prevAddress,
      [code]: value,
    }));
    hasUnsavedChanges.current = true;
  };

  const onChange = (event: Event) => {
    const target = event.target as HTMLInputElement;
    const { name, value } = target;

    updateField(name, value);

    validateFormElement(target);
  };

  const onInvalid = (event: Event) => {
    const target = event.target as HTMLInputElement | HTMLSelectElement;

    validateFormElement(target);
  };

  const onSelection = (event: Event) => {
    const target = event.target as HTMLSelectElement;
    const { name, value } = target;
    updateField(name, value);
    validateFormElement(target);
  };

  const onBlur = (event: Event) => {
    const target = event.target as HTMLInputElement | HTMLSelectElement;

    target.checkValidity();
  };

  // load default, preselected and backup address
  useEffect(() => {
    setAddress((prevAddress) => {
      return {
        ...defaultValues,
        ...preselection,
        ...addressBackup,
        ...prevAddress,
      };
    });
  }, [defaultValues, preselection, addressBackup]);

  // backup address
  useEffect(() => {
    if (!hasUnsavedChanges.current) return;
    const timerId = backup(address);
    return () => {
      clearTimeout(timerId);
    };
  }, [address, backup]);

  // sync address with the backend
  useEffect(() => {
    if (!hasUnsavedChanges.current || !isFormValid(formRef)) return;

    const controller = new AbortController();
    const signal = controller.signal;

    const timer = setTimeout(() => {
      saveAddressForm({ signal, address });
    }, DEBOUNCE_TIME);

    return () => {
      clearTimeout(timer);
      controller.abort();
    };
  }, [address, formRef, saveAddressForm]);

  return {
    address,
    setAddress,
    errors,
    onChange,
    onSelection,
    onBlur,
    onInvalid,
  };
};
