import { FormFields, getRegions } from '@/checkout/api';
import { FieldsFormSkeleton } from '@/checkout/components';
import {
  DefaultValues,
  MULTILINE_CUSTOM_ATTR_SPLIT,
  MULTILINE_SUFFIX,
  SaveAddressCommand,
  useAddressForm,
  usePreselectedFields,
} from '@/checkout/components/AddressForm';
import '@/checkout/components/AddressForm/AddressForm.css';
import { FieldsForm as Component } from '@/checkout/components/FieldsForm/FieldsForm';
import { PreselectedAddressFields } from '@/checkout/containers';
import {
  useStore,
  useAddressFormFields,
  useCountries,
} from '@/checkout/context';
import {
  AddressFormFields,
  AddressFormTypes,
  AddressFormValues,
  Region,
} from '@/checkout/data/models';
import { cartSignal } from '@/checkout/signals';
import { isEmpty, processFormFields } from '@/checkout/utils';
import { getFormErrors } from '@adobe/elsie/lib/form-values';
import {
  HTMLAttributes,
  forwardRef,
  useEffect,
  useImperativeHandle,
  useRef,
  useState,
} from 'preact/compat';
import { StateUpdater } from 'preact/hooks';

function useDefaultValues(defaultCountry: string | undefined) {
  const [defaultValues, setDefaultValues] = useState<DefaultValues>({});

  useEffect(() => {
    if (!defaultCountry) return;
    setDefaultValues((defaultValues) => ({
      ...defaultValues,
      country_id: defaultCountry,
    }));
  }, [defaultCountry]);

  return { defaultValues };
}

function useRegions({
  country,
  addressType,
}: {
  country: string | undefined;
  addressType: AddressFormTypes;
}) {
  const [availableRegions, setAvailableRegions] = useState<Region[]>([]);

  useEffect(() => {
    if (!country) {
      setAvailableRegions([]);
      return;
    }

    getRegions(country, addressType)
      .then((data) => {
        setAvailableRegions(data || []);
      })
      .catch((error) => {
        console.error(error);
      });
  }, [setAvailableRegions, country, addressType]);

  return { availableRegions };
}

function useFormFill({
  shouldAutoFillForm,
  addressType,
  setAddress,
  fields,
}: {
  shouldAutoFillForm?: () => boolean;
  addressType: AddressFormTypes;
  setAddress: StateUpdater<AddressFormValues>;
  fields?: FormFields;
}) {
  const [hasFormFillCompleted, setHasFormFillCompleted] = useState(false);
  const cartData = cartSignal.value.data;

  useEffect(() => {
    if (hasFormFillCompleted) return;

    const isReadyToProcess = cartData && fields;
    if (!isReadyToProcess) return;

    setHasFormFillCompleted(true);

    const addressData =
      addressType === AddressFormTypes.SHIPPING
        ? cartData?.shippingAddresses
        : cartData?.billingAddress;

    const isAnAddressList = Array.isArray(addressData);
    const hasAddressData = isAnAddressList
      ? addressData.length > 0
      : !!addressData;

    if (!hasAddressData) return;
    if (shouldAutoFillForm && !shouldAutoFillForm()) return;

    const isMultiline = (code: string) => {
      if (!fields) return false;
      const field = fields.find((field) => field.code === code);
      if (!field) return false;
      return field.frontend_input === 'MULTILINE';
    };

    const firstAddress = Array.isArray(addressData)
      ? addressData[0]
      : addressData;

    if (!firstAddress) return;

    const addressFormValues: AddressFormValues = {
      [AddressFormFields.City]: firstAddress.city,
      [AddressFormFields.Company]: firstAddress.company || '',
      [AddressFormFields.Country]: firstAddress.country.value,
      [AddressFormFields.FirstName]: firstAddress.firstName,
      [AddressFormFields.LastName]: firstAddress.lastName,
      [AddressFormFields.PostCode]: firstAddress.postCode || '',
      [AddressFormFields.Telephone]: firstAddress.telephone || '',
      [AddressFormFields.Vat]: firstAddress.vatId || '',
    };

    const region = firstAddress.region;

    if (region) {
      const regionId = region?.id?.toString();

      if (regionId) {
        addressFormValues[AddressFormFields.Region] = regionId;
        addressFormValues[AddressFormFields.RegionId] = regionId;
      } else {
        addressFormValues[AddressFormFields.Region] = region.code;
      }
    }

    if (firstAddress?.street && firstAddress.street.length > 0) {
      firstAddress.street.forEach((street, index) => {
        addressFormValues[
          `${AddressFormFields.Street}${MULTILINE_SUFFIX}${index}`
        ] = street!;
      });
    }

    const customAttributes = firstAddress?.customAttributes || [];

    customAttributes.forEach((attribute) => {
      if (!isMultiline(attribute.code)) {
        addressFormValues[attribute.code] = attribute.value;
      } else {
        const values = attribute.value.split(MULTILINE_CUSTOM_ATTR_SPLIT);
        values.forEach((value, index) => {
          addressFormValues[`${attribute.code}${MULTILINE_SUFFIX}${index}`] =
            value;
        });
      }
    });

    setAddress(addressFormValues);
  }, [
    setAddress,
    shouldAutoFillForm,
    addressType,
    cartData,
    fields,
    hasFormFillCompleted,
  ]);
}

export interface AddressFormProps extends HTMLAttributes<HTMLDivElement> {
  name: string;
  preselectedFields?: PreselectedAddressFields;
  addressType: AddressFormTypes;
  headingId: string;
  saveAddressHandler: (
    command: SaveAddressCommand
  ) => Promise<typeof cartSignal.value.data>;
  shouldAutoFillForm?: () => boolean;
}

export type AddressFormHandle = {
  triggerSaveAddress: (
    signal: AbortSignal
  ) => Promise<typeof cartSignal.value.data> | undefined;
};

export const AddressForm = forwardRef<AddressFormHandle, AddressFormProps>(
  (
    {
      name,
      preselectedFields,
      addressType,
      headingId,
      saveAddressHandler,
      children,
      shouldAutoFillForm,
      ...props
    },
    ref
  ) => {
    const { fields } = useAddressFormFields();
    const { countries: availableCountries } = useCountries();
    const isLoadingCountries = availableCountries === undefined;
    const isLoadingFormFields = fields === undefined;

    const { config } = useStore();
    const isLoadingStoreConfig = config === undefined;

    const { defaultValues } = useDefaultValues(config?.defaultCountry);

    const { preselection } = usePreselectedFields({
      fields,
      preselectedFields,
    });

    const formRef = useRef<HTMLFormElement>(null);

    const {
      address,
      setAddress,
      onSelection,
      errors,
      onChange,
      onInvalid,
      onBlur,
    } = useAddressForm({
      formRef,
      type: addressType,
      defaultValues,
      preselection,
      saveAddressHandler,
    });

    const { availableRegions } = useRegions({
      country: address.country_id,
      addressType,
    });

    useImperativeHandle(ref, () => ({
      triggerSaveAddress: (signal: AbortSignal) => {
        if (!formRef.current) return;
        const errors = getFormErrors(formRef.current);
        if (!isEmpty(errors)) return;
        return saveAddressHandler({ signal, address });
      },
    }));

    useFormFill({
      shouldAutoFillForm,
      addressType,
      setAddress,
      fields,
    });

    const isLoading =
      isLoadingFormFields || isLoadingCountries || isLoadingStoreConfig;

    if (isLoading) {
      return <FieldsFormSkeleton data-testid={`${addressType}-skeleton`} />;
    }

    const transformedFields = processFormFields({
      fields,
      address,
      setAddress,
      addressType,
      errors,
      config,
      availableCountries,
      availableRegions,
      onSelection,
      onChange,
      onBlur,
      onInvalid,
    });

    const typeMapping = {
      [AddressFormTypes.SHIPPING]: 'shipping',
      [AddressFormTypes.BILLING]: 'billing',
    };

    return (
      <Component
        {...props}
        data-testid={`${typeMapping[addressType]}-form`}
        name={name}
        className={`checkout-${typeMapping[addressType]}-form`}
        headingId={headingId}
        formRef={formRef}
        fields={transformedFields}
      />
    );
  }
);
