import { AttributeFrontendInputEnum } from '@/checkout/__generated__/types';
import { FormFields } from '@/checkout/api';
import {
  AddressFormErrors,
  AddressFormFields,
  AddressFormTypes,
  AddressFormValues,
  Country,
  Region,
  StoreConfig,
} from '@/checkout/data/models';
import { regionsSignal } from '@/checkout/signals';

import {
  getMultilineValues,
  refetchEstimatedShippingMethods,
} from '@/checkout/utils';
import { PickerOption } from '@adobe/elsie/src/components';
import { StateUpdater } from 'preact/hooks';

type FormFieldsProcessorProps = {
  fields: FormFields;
  address: AddressFormValues;
  setAddress: StateUpdater<AddressFormValues>;
  addressType: AddressFormTypes;
  errors: AddressFormErrors;
  config: StoreConfig;
  availableRegions: Region[];
  availableCountries: Country[] | undefined;
  onSelection: (event: Event) => void;
  onChange: (event: Event) => void;
  onBlur: (event: Event) => void;
  onInvalid: (event: Event) => void;
};

export type FormField = Omit<FormFields[number], 'options'> & {
  addressType: AddressFormTypes;
  className: string;
  disabled?: boolean;
  error: string | string[];
  handleSelect?: (event: Event) => void;
  id?: string;
  onBlur: (event: Event) => void;
  onChange: (event: Event) => void;
  onInvalid: (event: Event) => void;
  options?: PickerOption[];
  value: string | string[];
};

type FormFieldOptions = FormFields[number]['options'];

const isValidOption = (option: FormFieldOptions[number]) => {
  return option?.label && option?.value;
};

const transformFormFieldOptions = (data: FormFieldOptions): PickerOption[] => {
  if (!data) return [];

  return data.filter(isValidOption).map((option) => ({
    text: option!.label,
    value: option!.value,
  }));
};

const transformRegionOptions = (regions: Region[]): PickerOption[] => {
  return regions.map((region) => {
    const value = region?.id?.toString() || region.code;
    return { text: region.name, value };
  });
};

const transformCountryOptions = (
  countries: Country[] | undefined
): PickerOption[] => {
  if (!countries) return [];

  return countries.map((country) => ({
    text: country.label,
    value: country.value,
  }));
};

export function processFormFields({
  fields,
  address,
  setAddress,
  addressType,
  errors,
  config,
  availableCountries,
  availableRegions,
  onSelection,
  onChange,
  onBlur,
  onInvalid,
}: FormFieldsProcessorProps): FormField[] {
  const resetRegion = () => {
    setAddress((address) => ({
      ...address,
      [AddressFormFields.Region]: '',
      [AddressFormFields.RegionId]: '',
    }));
  };

  const syncRegionId = (regionId: string) => {
    setAddress((address) => ({
      ...address,
      [AddressFormFields.RegionId]: regionId,
    }));
  };

  return fields.map((field) => {
    let is_required = field.is_required;
    let value: string | string[];
    let inputType = field.frontend_input;
    let options: PickerOption[] = [];
    let error: string | string[];
    let handleSelect;
    let disabled = false;

    if (inputType === 'MULTILINE') {
      value = getMultilineValues(field.code, address);
      error = getMultilineValues(field.code, errors);
    } else {
      value = address[field.code];
      error = errors[field.code] || '';
    }

    if (
      field.code !== AddressFormFields.Region &&
      field.code !== AddressFormFields.Country
    ) {
      options = transformFormFieldOptions(field.options);
    }

    if (field.code === AddressFormFields.Country) {
      options = transformCountryOptions(availableCountries);
      if (addressType === AddressFormTypes.SHIPPING) {
        regionsSignal.value.country = value as string;

        handleSelect = (event: Event) => {
          const target = event.target as HTMLInputElement;
          const { value } = target;

          if (value) {
            refetchEstimatedShippingMethods({
              country_code: value,
            });
          }

          onSelection(event);
          resetRegion();
        };
      } else {
        handleSelect = onSelection;
      }
    }

    if (field.code === AddressFormFields.RegionId) {
      if (addressType === AddressFormTypes.SHIPPING) {
        regionsSignal.value.selectedRegionId = value as string;
      }
    }

    if (field.code === AddressFormFields.Region) {
      if (addressType === regionsSignal.value.addressType) {
        disabled = regionsSignal.value.pending;
      }

      is_required = config!.countriesWithRequiredRegion.includes(
        address?.country_id
      );
      options = transformRegionOptions(availableRegions);

      if (!is_required && !config!.displayStateIfOptional) {
        inputType = AttributeFrontendInputEnum.Undefined;
      }

      inputType =
        options.length > 0
          ? AttributeFrontendInputEnum.Select
          : AttributeFrontendInputEnum.Text;

      if (inputType === 'SELECT') {
        if (addressType === AddressFormTypes.SHIPPING) {
          regionsSignal.value.selectedRegion = value as string;

          handleSelect = (event: Event) => {
            const target = event.target as HTMLInputElement;
            const selectedRegion = target.value;

            refetchEstimatedShippingMethods({
              country_code: regionsSignal.value.country as string,
              region_id: selectedRegion,
            });

            onSelection(event);
            syncRegionId(selectedRegion);
          };
        } else {
          handleSelect = (event: Event) => {
            onSelection(event);
            const target = event.target as HTMLSelectElement;
            const selectedRegion = target.value;
            syncRegionId(selectedRegion);
          };
        }
      } else if (inputType === 'TEXT') {
        if (addressType === AddressFormTypes.SHIPPING) {
          regionsSignal.value.selectedRegion = value as string;

          const defaultOnChange = onChange;

          onChange = (event: Event) => {
            const target = event.target as HTMLInputElement;
            const { value } = target;

            if (regionsSignal.value.country) {
              refetchEstimatedShippingMethods({
                country_code: regionsSignal.value.country as string,
                region_name: value as string,
              });
            }

            defaultOnChange(event);
          };
        }
      }

      value =
        options.length > 0
          ? options.find((regionOption) => regionOption!.value === value)
              ?.value || ''
          : value;
    }

    if (field.code === AddressFormFields.PostCode) {
      is_required = !config!.countriesWithOptionalZipCode.includes(
        address?.country_id as string
      );
    }

    return {
      ...field,
      is_required,
      frontend_input: inputType,
      addressType,
      className: `checkout-address-form__${field.code}`,
      value,
      options,
      error,
      onChange,
      onBlur,
      onInvalid,
      handleSelect,
      disabled,
    };
  });
}

export type ProcessedFormFields = ReturnType<typeof processFormFields>;
