import { cartSignal } from '@/checkout/signals';
import { DEBOUNCE_TIME } from '@/checkout/components/AddressForm/constants';
import { ShippingEstimationCriteria, estimateShippingMethods } from '@/checkout/api';

let estimateShipppingTimer: ReturnType<typeof setTimeout>;
export function refetchEstimatedShippingMethods(
  criteria: ShippingEstimationCriteria
) {
  const cartData = cartSignal.value.data;
  const hasCartData = !!cartData;
  const firstAddress = cartData?.shippingAddresses?.[0];
  const availableShippingMethods = firstAddress?.availableShippingMethods;

  if (hasCartData && !availableShippingMethods) {
    clearTimeout(estimateShipppingTimer);

    estimateShipppingTimer = setTimeout(() => {
      estimateShippingMethods({ cartId: cartData.id, criteria });
    }, DEBOUNCE_TIME);
  }
}
