import { FunctionComponent, Ref } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { classes } from '@adobe/elsie/lib';
import '@/checkout/components/FieldsForm/FieldsForm.css';
import { createField } from './fieldFactory';
import { Heading } from '@/checkout/components';
import { Text } from '@adobe/elsie/i18n';
import { ProcessedFormFields } from '@/checkout/utils';

export type BaseField = ProcessedFormFields[number] & {
  id?: string;
  disabled?: boolean;
  handleSelect?: (event: Event) => void;
};

export interface FieldsFormProps extends HTMLAttributes<HTMLDivElement> {
  headingId: string;
  name: string;
  formRef: Ref<HTMLFormElement>;
  fields: BaseField[];
}

export const FieldsForm: FunctionComponent<FieldsFormProps> = ({
  className,
  name,
  headingId,
  formRef,
  fields,
  ...props
}) => {
  return (
    <div {...props} className={classes(['checkout-fields-form', className])}>
      <Heading
        level={2}
        children={<Text id={headingId} />}
        className="checkout-fields-form__title"
      />

      <form
        name={name}
        ref={formRef}
        className={classes(['checkout-fields-form__form', className])}
        noValidate
      >
        {fields
          .sort((a, b) => {
            return a.sort_order! - b.sort_order!;
          })
          .map(createField)}
      </form>
    </div>
  );
};
