import { ValidationRule } from '@/checkout/__generated__/types';
import { BaseField, validityProps } from '@/checkout/components/FieldsForm';
import autocompleteFieldCodes, {
  fieldCodes,
} from '@/checkout/utils/autocompleteFieldCodes/autocompleteFieldCodes';
import { Field, Input, Picker } from '@adobe/elsie/components';

const hiddenField = (field: BaseField) => {
  const { id, addressType, className, code, value, default_value } = field;
  const fieldID = id || code;

  return (
    <input
      className={className}
      id={`${addressType}-${fieldID}`}
      key={code}
      name={code}
      type="hidden"
      value={value ?? default_value}
    />
  );
};

const textField = (field: BaseField) => {
  const {
    addressType,
    className,
    code,
    error,
    id,
    onBlur,
    onChange,
    onInvalid,
    validate_rules,
    value,
  } = field;

  const validity = validityProps(validate_rules as ValidationRule[]);
  const fieldID = id || code;

  return (
    <Field disabled={field.disabled} error={error as string}>
      <Input
        aria-label={field.label!}
        autocomplete={autocompleteFieldCodes[code as keyof fieldCodes] || 'off'}
        className={className}
        floatingLabel={`${field.label} ${field.is_required ? '*' : ''}`}
        id={`${addressType}-${fieldID}`}
        onBlur={onBlur}
        onChange={onChange}
        onInvalid={onInvalid}
        placeholder={field.label!}
        required={field.is_required || false}
        type="text"
        // @ts-ignore
        name={code}
        // @ts-ignore
        value={value ?? undefined}
        {...validity}
      />
    </Field>
  );
};

const multilineField = (field: BaseField) => {
  const {
    addressType,
    className,
    code,
    disabled,
    error,
    id,
    is_required,
    label,
    multiline_count,
    onBlur,
    onChange,
    onInvalid,
    validate_rules,
    value,
  } = field;

  const numOfFields = multiline_count ?? 0;
  const fieldID = id || code;
  const validity = validityProps(validate_rules as ValidationRule[]);

  return (
    <>
      {Array.from(Array(numOfFields).keys()).map((i) => (
        <Field
          disabled={disabled}
          error={(error as string[])?.[i] || ''}
          key={`${code}-${i}`}
          className="dropin-field--multiline"
        >
          <Input
            id={`${addressType}-${fieldID}-${i}`}
            className={className}
            floatingLabel={`${label} ${i != 0 ? i : ''} ${
              is_required && i === 0 ? '*' : ''
            }`}
            autocomplete={
              i === 0 ? autocompleteFieldCodes[code as keyof fieldCodes] : 'off'
            }
            aria-label={label!}
            placeholder={`${label} ${i != 0 ? i : ''}`}
            type="text"
            required={is_required && i === 0}
            onChange={onChange}
            onBlur={onBlur}
            onInvalid={onInvalid}
            // @ts-ignore
            name={`${code}-${i}`}
            // @ts-ignore
            value={(value as string[])?.[i] || undefined}
            {...validity}
          />
        </Field>
      ))}
    </>
  );
};

const selectField = (field: BaseField) => {
  const {
    addressType,
    className,
    code,
    disabled,
    error,
    handleSelect,
    id,
    is_required,
    label,
    onBlur,
    onInvalid,
    options,
    value,
  } = field;

  const fieldID = id || code;
  const handleSelectAttribute = handleSelect ? { handleSelect } : {};

  return (
    <Field disabled={disabled} error={error as string}>
      <Picker
        id={`${addressType}-${fieldID}`}
        className={className}
        key={code}
        name={code}
        floatingLabel={`${label} ${is_required ? '*' : ''}`}
        required={is_required}
        placeholder={label!}
        aria-label={label!}
        options={options}
        value={value as string}
        autocomplete={autocompleteFieldCodes[code as keyof fieldCodes] || 'off'}
        onBlur={onBlur}
        onInvalid={onInvalid}
        {...handleSelectAttribute}
      />
    </Field>
  );
};

export const createField = (field: BaseField) => {
  switch (field.frontend_input) {
    case 'BOOLEAN':
    case 'DATE':
    case 'DATETIME':
    case 'FILE':
    case 'GALLERY':
    case 'IMAGE':
    case 'MEDIA_IMAGE':
    case 'MULTISELECT':
    case 'PRICE':
    case 'TEXTAREA':
    case 'UNDEFINED':
    case 'WEIGHT':
      return null;
    case 'HIDDEN':
      return hiddenField(field);
    case 'TEXT':
      return textField(field);
    case 'MULTILINE':
      return multilineField(field);
    case 'SELECT':
      return selectField(field);
    default:
      throw new Error(`Unknown field type: ${field.frontend_input}`);
  }
};
