import { FormFields } from '@/checkout/api';
import { PreselectedAddressFields } from '@/checkout/containers';
import { useMemo } from 'preact/hooks';

type PreselectedFieldKey = keyof PreselectedAddressFields;

export type PreselectedMappedField = {
  country_id?: string;
  region?: string;
  postcode?: string;
};

type PreselectedMappedFieldKey = keyof PreselectedMappedField;

const FIELD_CODE_MAPPING: {
  [key in PreselectedFieldKey]: PreselectedMappedFieldKey;
} = {
  countryCode: 'country_id',
  region: 'region',
  postCode: 'postcode',
};

export type UsePreselectedFieldsProps = {
  fields?: FormFields;
  preselectedFields?: PreselectedAddressFields;
};

export function usePreselectedFields({
  fields,
  preselectedFields,
}: UsePreselectedFieldsProps) {
  const preselection =
    useMemo(() => {
      const hasFields = !!fields && fields.length > 0;
      const hasPreselectedFields = !!preselectedFields;

      if (!hasFields || !hasPreselectedFields) return null;

      return Object.keys(preselectedFields).reduce((acc, code) => {
        const formCode = FIELD_CODE_MAPPING[code as PreselectedFieldKey];
        if (!formCode) return acc;

        const formField = fields.some((field) => field.code === formCode);
        if (!formField) return acc;

        return {
          ...acc,
          [formCode]: preselectedFields[code as PreselectedFieldKey],
        };
      }, {} as PreselectedMappedField);
    }, [fields, preselectedFields]);

  return { preselection };
}
