import { useEffect, useState } from 'preact/hooks';

export const useBreakpoint = () => {
  const getBreakpoint = () => {
    if (window.innerWidth >= 1920) return 'xxlarge';
    if (window.innerWidth >= 1366) return 'xlarge';
    if (window.innerWidth >= 1024) return 'large';
    if (window.innerWidth >= 768) return 'medium';

    return 'small';
  };

  const [breakpoint, setBreakpoint] = useState<
    ReturnType<typeof getBreakpoint>
  >(getBreakpoint());

  useEffect(() => {
    let timeoutId: ReturnType<typeof setTimeout>;

    const handleResize = () => {
      if (timeoutId) clearTimeout(timeoutId);
      timeoutId = setTimeout(() => setBreakpoint(getBreakpoint()), 50);
    };

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
      if (timeoutId) clearTimeout(timeoutId);
    };
  }, []);

  return breakpoint;
};
