import { useCallback, useState } from 'preact/hooks';

export type Translations = {
  badInput: string;
  patternMismatch: string;
  rangeOverflow: string;
  rangeUnderflow: string;
  tooLong: string;
  tooShort: string;
  typeMismatch: string;
  valueMissing: string;
};

type FormElement = HTMLInputElement | HTMLSelectElement;

type Constraint =
  | 'badInput'
  | 'patternMismatch'
  | 'rangeOverflow'
  | 'rangeUnderflow'
  | 'tooLong'
  | 'tooShort'
  | 'typeMismatch'
  | 'valueMissing';

type Attribute = 'aria-label' | 'maxlength' | 'minlength' | 'min' | 'max';

const attributesMapping: Record<Constraint, Attribute> = {
  badInput: 'aria-label',
  patternMismatch: 'aria-label',
  rangeOverflow: 'max',
  rangeUnderflow: 'min',
  tooLong: 'maxlength',
  tooShort: 'minlength',
  typeMismatch: 'aria-label',
  valueMissing: 'aria-label',
};

const constraints: Constraint[] = [
  'badInput',
  'patternMismatch',
  'rangeOverflow',
  'rangeUnderflow',
  'tooLong',
  'tooShort',
  'typeMismatch',
  'valueMissing',
];

export const useValidity = (translations: Translations) => {
  const [errors, setErrors] = useState<Record<string, string>>({});

  const validateFormElement = useCallback(
    (formElement: FormElement) => {
      const { name, validity, validationMessage } = formElement;

      let error = validity.valid ? '' : validationMessage;

      constraints.forEach((constraint) => {
        if (!validity[constraint]) return;
        const translation = translations[constraint];
        if (!translation) return;
        const attribute = attributesMapping[constraint];
        error = translation.replace(
          '{field}',
          formElement.getAttribute(attribute) || ''
        );
      });

      setErrors((prevErrors) => ({ ...prevErrors, [name]: error }));
    },
    [translations]
  );

  return {
    errors,
    validateFormElement,
  };
};
