import { Icon } from '@adobe/elsie/components';
import '@adobe/elsie/components/Picker/Picker.css';
import { ChevronDown } from '@adobe/elsie/icons';
import { classes } from '@adobe/elsie/lib';
import { FunctionComponent, VNode } from 'preact';
import { HTMLAttributes, useEffect, useState } from 'preact/compat';
import { useRef } from 'preact/hooks';

export interface PickerOption {
  value: string | null;
  text?: string;
  icon?: VNode<HTMLAttributes<SVGSVGElement>>;
  disabled?: boolean;
}

export interface PickerProps
  extends Omit<HTMLAttributes<HTMLSelectElement>, 'value' | 'size' | 'icon'> {
  id?: string;
  name?: string;
  value?: string | null;
  placeholder?: string;
  variant?: 'primary' | 'secondary';
  size?: 'medium' | 'large';
  floatingLabel?: string;
  icon?: VNode<HTMLAttributes<SVGSVGElement>>;
  options?: PickerOption[];
  defaultOption?: PickerOption;
  disabled?: boolean;
  error?: boolean;
  handleSelect?: (event: Event) => void;
}

export const Picker: FunctionComponent<PickerProps> = ({
  name,
  value = null,
  options,
  variant = 'primary',
  floatingLabel,
  size = 'medium',
  handleSelect = () => {},
  disabled = false,
  error = false,
  placeholder,
  defaultOption,
  icon,
  className,
  ...props
}) => {
  const id = props?.id || name || `dropin-picker-${Math.random().toString(36)}`;

  // find the first option that is not disabled
  const firstAvailableeOption = options?.find((option) => !option.disabled);

  const [selectedValue, setSelectedValue] = useState<PickerOption>({
    value,
    text: defaultOption?.text || placeholder || firstAvailableeOption?.text,
    icon,
  });

  useEffect(() => {
    const selected = options?.find((option) => option.value === value);

    setSelectedValue(
      selected ?? {
        value:
          (defaultOption?.value as string) ||
          ((placeholder || floatingLabel
            ? null
            : firstAvailableeOption?.value) as string),
        text: defaultOption?.text || placeholder || firstAvailableeOption?.text,
        icon,
      }
    );
  }, [value, options, placeholder, icon, defaultOption, floatingLabel]);

  const handleOptionClick = (event: Event) => {
    const { options, value } = event.target as HTMLSelectElement;

    for (const element of options) {
      if (element.selected) {
        setSelectedValue({ value, text: element.text, icon });
        handleSelect(event);
      }
    }
  };

  const optionsListItems = options?.map((option) => {
    const { value, text, disabled } = option;
    const selected = value === selectedValue.value;

    return (
      <option
        value={value as string}
        key={value}
        selected={selected}
        disabled={disabled}
        className={classes(['dropin-picker__option'])}
      >
        {text}
      </option>
    );
  });

  const placeholderOptionRef = useRef(null);
  // @ts-ignore
  const selected = !placeholderOptionRef.current?.selected || !!defaultOption;

  return (
    <div
      className={classes([
        className,
        'dropin-picker',
        `dropin-picker__${size}`,
        ['dropin-picker__floating', !!floatingLabel],
        ['dropin-picker__selected', selected],
        ['dropin-picker__error', error],
        ['dropin-picker__disabled', disabled],
        ['dropin-picker__icon', icon],
      ])}
    >
      {icon && (
        // @ts-ignore
        <icon.type
          {...icon.props}
          className={'dropin-picker__icon--placeholder'}
        />
      )}

      <select
        id={id}
        className={classes([
          'dropin-picker__select',
          `dropin-picker__select--${variant}`,
          `dropin-picker__select--${size}`,
          ['dropin-picker__select--floating', !!floatingLabel],
        ])}
        name={name}
        aria-label={name}
        disabled={disabled}
        onChange={handleOptionClick}
        {...props}
      >
        {!defaultOption && (floatingLabel || placeholder) && (
          <option
            value={''}
            key={value}
            ref={placeholderOptionRef}
            className={classes([
              'dropin-picker__option dropin-picker__placeholder',
            ])}
          >
            {floatingLabel ?? placeholder}
          </option>
        )}

        {optionsListItems}
      </select>

      <Icon
        source={ChevronDown}
        size="24"
        stroke="2"
        className="dropin-picker__chevronDown"
      />

      {floatingLabel && selected && (
        <label
          htmlFor={id}
          className={classes(['dropin-picker__floatingLabel', !!floatingLabel])}
        >
          {floatingLabel}
        </label>
      )}
    </div>
  );
};
